import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import apiService from '../services/api';
import './Auth.css';

const ResetPassword: React.FC = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [tokenValid, setTokenValid] = useState<boolean | null>(null);
  const token = searchParams.get('token');

  useEffect(() => {
    // Validate token on mount
    if (!token) {
      setTokenValid(false);
      setError('ifre sfrlama linki geersiz veya eksik');
      return;
    }

    // Token validation will happen on backend when submitting
    setTokenValid(true);
  }, [token]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    // Validation
    if (!password.trim() || !confirmPassword.trim()) {
      setError('Ltfen tm alanlar doldurun');
      return;
    }

    if (password.length < 8) {
      setError('ifre en az 8 karakter olmaldr');
      return;
    }

    if (password !== confirmPassword) {
      setError('ifreler elemiyor');
      return;
    }

    if (!token) {
      setError('Geersiz sfrlama token');
      return;
    }

    setIsLoading(true);

    try {
      const response = await apiService.resetPassword(token, password);
      toast.success(response.message || 'ifreniz baaryla sfrland!');
      setTimeout(() => navigate('/login'), 1500);
    } catch (err: any) {
      setError(err.response?.data?.detail || 'ifre sfrlama baarsz oldu. Token geersiz veya sresi dolmu olabilir.');
      setTokenValid(false);
    } finally {
      setIsLoading(false);
    }
  };

  if (tokenValid === null) {
    return (
      <div className="auth-container">
        <header className="auth-header-bar">
          <h1 className="auth-logo" onClick={() => navigate('/')}>MemoLink</h1>
          <div className="auth-nav">
            <button className="btn-nav-secondary" onClick={() => navigate('/login')}>
              Giri Yap 
            </button>
          </div>
        </header>

        <div className="auth-main">
          <div className="auth-content">
            <h2 className="auth-title">Token Dorulanyor...</h2>
            <p className="auth-subtitle">Ltfen bekleyin</p>
          </div>
        </div>
      </div>
    );
  }

  if (tokenValid === false) {
    return (
      <div className="auth-container">
        <header className="auth-header-bar">
          <h1 className="auth-logo" onClick={() => navigate('/')}>MemoLink</h1>
          <div className="auth-nav">
            <button className="btn-nav-secondary" onClick={() => navigate('/login')}>
              Giri Yap 
            </button>
          </div>
        </header>

        <div className="auth-main">
          <div className="auth-content">
            <h2 className="auth-title">Geersiz veya Sresi Dolmu Token</h2>
            <p className="auth-subtitle">{error}</p>

            <form className="auth-form">
              <button className="auth-button" onClick={() => navigate('/forgot-password')}>
                Yeni Sfrlama Linki ste
              </button>

              <div className="auth-footer">
                <span className="auth-link" onClick={() => navigate('/login')}>
                  Giri Yap
                </span>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="auth-container">
      <header className="auth-header-bar">
        <h1 className="auth-logo" onClick={() => navigate('/')}>MemoLink</h1>
        <div className="auth-nav">
          <span>Zaten hesabn var m?</span>
          <button className="btn-nav-secondary" onClick={() => navigate('/login')}>
            Giri Yap 
          </button>
        </div>
      </header>

      <div className="auth-main">
        <div className="auth-content">
          <h2 className="auth-title">ifreni Sfrla</h2>
          <p className="auth-subtitle">Yeni ifreni belirle</p>

          <form className="auth-form" onSubmit={handleSubmit}>
            {error && <div className="auth-error">{error}</div>}

            <div className="form-group">
              <label className="form-label">Yeni ifre</label>
              <input
                type="password"
                className="form-input"
                placeholder="Yeni ifreni gir (min. 8 karakter)"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                disabled={isLoading}
              />
            </div>

            <div className="form-group">
              <label className="form-label">ifre Tekrar</label>
              <input
                type="password"
                className="form-input"
                placeholder="ifreni tekrar gir"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                disabled={isLoading}
              />
            </div>

            <button type="submit" className="auth-button" disabled={isLoading}>
              {isLoading ? 'ifre sfrlanyor...' : 'ifremi Sfrla'}
            </button>

            <div className="auth-footer">
              <span className="auth-link" onClick={() => navigate('/login')}>
                Giri Yap
              </span>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ResetPassword;
